import React, {Component} from 'react';
import { Link } from 'react-scroll';
import { FaGithub, FaLinkedin, FaGoogle } from "react-icons/fa";
import { IconContext } from "react-icons";
import { MdEmail, MdMailOutline } from "react-icons/md";
class Homepage extends Component {
  constructor(props){
    super(props);
    this.state = {
      x:0,
      y:0
    }
  }
  _onMouseMove = (e) => {
    const width = this.refs.titleContainer.clientWidth;
    const height = this.refs.titleContainer.clientHeight;
    const oX = (e.nativeEvent.offsetX/width)*100;
    const oY = (e.nativeEvent.offsetY/height)*100;
    //console.log(e.nativeEvent);
    this.setState({
      x: oX,
      y: oY
    });
    //console.log(e.nativeEvent); **e.nativeEvent gives us a lot of info about an event
  }

  render(){
    const {x, y} = this.state;
    const maskStyle = {
      '--maskX': x,
      '--maskY': y
    }
  	return (
        <div className="Homepage" id = "homePage">
        <div className="Photo">
          <img src={require('../../Photos/IMG_1385.jpeg')}/>
        </div>
        <div className="titleContainer"
          onMouseMove={this._onMouseMove}
          ref="titleContainer"
          style={maskStyle}
        >
          <div className="titleWrapper"><h1>Computer Engineering UCLA '22</h1></div>
        </div>
          <h2>	&mdash;&ensp; Software Engineer building a better world.&ensp;&mdash;</h2>
          <div className="LogoSets2">
            <IconContext.Provider value={{ color: "white", size: "3.2vw" }}>
              <div>
                <a href="https://github.com/melodychn" target="_blank"><FaGithub /></a>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <a href="https://www.linkedin.com/in/melody-chen-179732144/" target="_blank"><FaLinkedin /></a>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <a href="mailto:melodyc1205@ucla.edu" target="_blank"><MdMailOutline /></a>
              </div>
            </IconContext.Provider>
          </div>
        <div className="Arrow"><Link
          activeClass="active"
          to="aboutme"
          spy={true}
          smooth={true}
          offset={0}
          duration= {500}><img src={require('../../Photos/scrolldown.png')} height="50"/></Link></div>
        </div>
  );
}
}

export default Homepage;
